.popup-search__body {
    padding: 0 50px;
    min-height: 40vmin;
    @media (max-width: 500px) {
        padding: 0 20px;
    }
}
.popup-search__form {
    position: relative;
    margin-bottom: 20px;
}
.popup-search__input {
    padding: 10px 50px 10px 20px;
    height: 42px;
    width: 100%;
    border: 2px solid #eaeaf0;
    border-radius: 8px;
    color: #070c11;
    line-height: 120%;
    font-size: 16px;
    background-color: #fff;
    &::placeholder {
        color: #838688;
        line-height: 120%;
        font-size: 16px;
    }
    &:focus-visible {
        border: 2px solid #4878a6;
    }
}
.popup-search__btn {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    background: transparent;
}
.popup-search__results {
}
.search-results {
    max-height: 40vmin;
    overflow-y: auto;
    &__list {
    }
    &__item {
        padding: 10px 0;
        &:not(:last-child) {
            border-bottom: 1px solid #4878a6;
        }
    }
}
.search-result {
    display: grid;
    column-gap: 20px;
    grid-template-columns: 90px 1fr;
    grid-template-rows: 1fr 1fr;
    &:focus-visible {
        outline: 2px solid #4878a6;
        border-radius: 2px;
    }
    @media (any-hover: hover) {
        &:hover {
            .search-result__image {
                transform: scale(1.1);
            }
            .search-result__title {
                color: #4878a6;
            }
        }
    }
    &__image {
        width: 100%;
        height: 70px;
        object-fit: contain;
        grid-row: 1 / 3;
        transition: transform 0.3s;
    }
    &__title {
        color: #070c11;
        transition: color 0.3s;
    }
    &__price {
        color: #070c11;
        font-weight: 700;
    }
}
