@import "../../assets/styles/variables.scss";
.user-authorized {
    // .user-authorized__button
    &__button {
        border: none;
        background: none;
        color: #838688;
        transition: color 0.3s;
        &:focus-visible {
            outline: 2px solid #2a5275;
            border-radius: 1px;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #2a5275;
            }
        }
        svg {
            color: inherit;
        }
    }
    // .user-authorized__dropdown
    &__dropdown {
    }
    // .user-authorized__popover
    &__popover {
    }
    // .user-authorized__email
    &__email {
        padding: 15px 20px;
        font-weight: 700;
        font-size: 18px;
        color: #4878a6;
        border-bottom: 1px solid #eaeaf0;
    }
    // .user-authorized__options
    &__options {
    }
    // .user-authorized__option
    &__option {
        &:last-child {
            border-top: 1px solid #eaeaf0;
        }
    }
    // .user-authorized__link
    &__link {
        display: block;
        padding: 15px 20px;
        min-width: 280px;
        font-family: $fontFamily;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #070c11;
        background-color: #fff;
        transition: color 0.3s, background-color 0.3s;
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: -2px;
            border-radius: 2px;
            color: #4878a6;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
                background-color: rgba(#dae4ed, 0.5);
            }
        }
    }
    // .user-authorized__logout
    &__logout {
        display: block;
        width: 100%;
        padding: 15px 20px;
        font-family: $fontFamily;
        font-weight: 500;
        font-size: 16px;
        text-align: left;
        color: #f15152;
        background-color: #fff;
        transition: color 0.3s, background-color 0.3s;
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: -2px;
            border-radius: 2px;
            color: #be1515;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #be1515;
                background-color: #dae4ed;
            }
        }
    }
}
