.item-input {
    position: relative;
    min-height: 95px;
    padding-bottom: 20px;
    &__title {
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }
    &__label {
        span {
            display: inline-block;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
    &__input {
        border: 2px solid #eaeaf0;
        width: 100%;
        border-radius: 5px;
        height: 48px;
        padding: 0 20px;
        color: #070c11;
        font-size: 16px;
        transition: border 0.3s;
        &:focus-visible {
            border: 2px solid #4878a6;
        }
    }
    &__error {
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 14px;
        color: rgb(196, 40, 40);
    }
    &__textarea {
        resize: vertical;
        border: 2px solid #eaeaf0;
        width: 100%;
        border-radius: 5px;
        min-height: 48px;
        padding: 10px 20px;
        color: #070c11;
        font-size: 16px;
        transition: border-color 0.3s;
        &:focus-visible {
            border: 2px solid #4878a6;
        }
    }
}
