.modal-close-btn {
    cursor: pointer;
    flex: 0 0 18px;
    height: 18px;
    background-color: transparent;
    background-size: 18px;
    background-repeat: no-repeat;
    background-position: center;
    transition: all 0.3s;
    @media (any-hover: hover) {
        &:hover {
            background-size: 20px;
        }
    }
    &:focus-visible {
        outline: 2px solid #2a5275;
        outline-offset: 2px;
        border-radius: 2px;
        transition: none;
    }
}
.popup {
    &__header {
        display: flex;
        align-items: center;
        padding: 0 50px;
        margin-bottom: 30px;
        @media (max-width: 500px) {
            padding: 0 20px;
        }
    }
    &__title {
        flex: 1 1 auto;
        padding-right: 30px;
    }
}
.popup-small {
    // .popup-small__header
    &__header {
        padding: 20px 30px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background-color: #edf2f6;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    // .popup-small__title
    &__title {
        margin-right: 20px;
    }
}