.spinner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    min-height: 52vh;
    padding: 20px;
    margin: 0 auto;
    svg {
        width: 100%;
        path {
            animation: 3s ease-in-out infinite alternate;
            transition: stroke-dashoffset 0.3s, fill 0.3s ease-in-out;
            &:first-child {
                stroke: #4878a6;
                stroke-width: 0.5px;
                stroke-dasharray: 100;
                animation-name: strokeAnimation1;
            }
            &:last-child {
                stroke: #2a5276;
                stroke-width: 0.5px;
                stroke-dasharray: 100;
                animation-name: strokeAnimation2;
            }
        }
    }
}
@keyframes strokeAnimation1 {
    0%,
    100% {
        stroke-dashoffset: 100;
        fill: rgba(#4878a6, 0.05);
    }
    50% {
        stroke-dashoffset: 0;
        fill: #4878a6;
    }
}
@keyframes strokeAnimation2 {
    0%,
    100% {
        stroke-dashoffset: 100;
        fill: rgba(#2a5276, 0.05);
    }
    50% {
        stroke-dashoffset: 0;
        fill: #2a5276;
    }
}
