.footer {
    background-color: #EDF2F6;
    padding: 40px 0 34px;
    @media (max-width: 768px) {
        padding: 40px 0;
    }
    // .footer__inner
    &__inner {
    }
    // .footer__inner-top
    &__inner-top {
        margin-bottom: 40px;
    }
    // .footer__inner-bottom
    &__inner-bottom {
    }
}
.top-footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 992px) {
        justify-content: unset;
        grid-gap: 50px 130px;
    }
    @media (max-width: 768px) {
        grid-gap: 40px 130px;
    }
    // .top-footer__contacts
    &__contacts {
        // margin-right: 30px;
        @media (max-width: 992px) {
            flex-basis: 250px;
        }
    }
    // .top-footer__logo-link
    &__logo-link {
        display: inline-block;
        margin-bottom: 20px;
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 2px;
            transition: none;
        }
    }
    // .top-footer__logo-img
    &__logo-img {
    }
    // .top-footer__contacts-phone
    &__contacts-phone {
        display: block;
        max-width: fit-content;
        font-weight: 600;
        font-size: 20px;
        line-height: 130%;
        color: #070C11;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
                transition: color .3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 2px;
            color: #4878a6;
            transition: color .3s, outline 0s;
        }
    }
    // .top-footer__contacts-schedule
    &__contacts-schedule {
        color: #838688;
        line-height: 130%;
        margin-bottom: 20px;
    }
    // .top-footer__contacts-adress
    &__contacts-adress {
        display: block;
        max-width: fit-content;
        white-space: nowrap;
        color: #070C11;
        line-height: 130%;
        &:not(:last-child) {
            margin-bottom: 3px;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
                transition: color .3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 2px;
            color: #4878a6;
            transition: color .3s, outline 0s;
        }
    }
    // .top-footer__box
    &__box {
        @media (max-width: 992px) {
            flex-basis: 250px;
        }
    }
    // .top-footer__box-title
    &__box-title {
        font-size: 20px;
        font-weight: 600;
        line-height: 130%;
        color: #838688;
        margin-bottom: 20px;
    }
    // .top-footer__item
    &__item {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
    // .top-footer__link
    &__link {
        line-height: 130%;
        color: #070C11;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
                transition: color .3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 2px;
            color: #4878a6;
            transition: color .3s, outline 0s;
        }
    }
}
.bottom-footer {
    padding-top: 38px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    border-top: 1px solid #C8CACB;
    @media (max-width: 768px) {
       padding-top: 34px;
    }
    // .bottom-footer__copyright
    &__copyright {
        color: #838688;
        margin-right: 20px;
        @media (max-width: 768px) {
            flex: 0 0 50%;
            font-size: 14px;
        }
        @media (max-width: 600px) {
            flex: 0 0 100%;
            order: 2;
            text-align: center;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
    // .bottom-footer__list-social
    &__list-social {
        display: flex;
        align-items: center;
        margin-right: 20px;
        @media (max-width: 768px) {
            flex: 0 1 200px;
            margin-left: auto;
            margin-right: 0;
            align-self: center;
        }
        @media (max-width: 600px) {
            flex: 0 0 100%;
            order: 1;
            max-width: fit-content;
            margin-right: auto;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
    // .bottom-footer__item
    &__item {
        &:not(:last-child) {
            margin-right: 30px;
        }
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.25);
                transition: transform .3s;
            }
        }
    }
    // .bottom-footer__link
    &__link {
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 5px;
            border-radius: 4px;
            transition: outline 0s;
        }
    }
    // .bottom-footer__author
    &__author {
        color: #838688;
        font-size: 14px;
        @media (max-width: 768px) {
            flex: 0 0 50%;
        }
        @media (max-width: 600px) {
            flex: 0 0 100%;
            order: 3;
            text-align: center;
        }
    }
}