.top-header__login-btn {
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    white-space: nowrap;
    padding: 14px 25px;
    background-color: #2a5275;
    border-radius: 4px;
    transition: background-color 0.5s;
    &:hover {
        background-color: #4878a6;
    }
    &:disabled {
        pointer-events: none;
    }
    &:focus-visible {
        outline: 2px solid #000000;
        outline-offset: 2px;
        border-radius: 4px;
        background-color: #4878a6;
    }
    @media (max-width: 500px) {
        padding: 13px 20px;
    }
}
.header-auth-transition {
    &-enter {
        opacity: 0;
        visibility: hidden;
        transform: translateX(20px);
    }
    &-enter-active {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        transition: all 0.5s;
    }
    &-exit {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }
    &-exit-active {
        opacity: 0;
        visibility: hidden;
        transform: translateX(20px);
        transition: all 0.5s;
    }
}
