.popup {
    &__container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 20;
        padding: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media(max-width: 450px) {
            padding: 10px;
        }
        @media(max-width: 320px) {
            padding: 10px 0px;
        }
    }
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-color: rgba(42, 82, 117, 0.6);
        cursor: pointer;
        &.transparent {
            background-color: transparent;
        }
    }
}
.popup-backdrop {
    &-enter {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
    &-enter-active {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        transition: all .5s, pointer-events 0s;
    }
    &-exit {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
    &-exit-active {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transition: all .5s, pointer-events 0s;
    }
}
.popup-body {
    &-appear {
        opacity: 0;
        visibility: hidden;
        transform: translateY(50%);
    }
    &-appear-active {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        transition: all .5s;
    }
    &-enter {
        opacity: 0;
        visibility: hidden;
        transform: translateY(50%);
    }
    &-enter-active {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        transition: all .5s;
    }
    &-exit {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }
    &-exit-active {
        opacity: 0;
        visibility: hidden;
        transform: translateY(50%);
        transition: all .5s;
    }
}
