.popup-more__body {
    padding: 0 30px;
    @media (max-width: 500px) {
        padding: 0 10px;
    }
}
.popup-more {
    // .popup-more__user-actions
    &__user-actions {
        @media (max-width: 600px) {
            display: flex;
            align-items: center;
            padding: 20px;
        }
    }
    // .popup-more__user-action
    &__user-action {
        flex: 0 1 48px;
        &:not(:last-child) {
            margin-right: 20px;
        }
    }
}
.action-user {
    position: relative;
    display: none;
    @media (max-width: 600px) {
        display: block;
    }
    // .action-user__link
    &__link {
    }
    // .action-user__quantity
    &__quantity {
        position: absolute;
        left: 15px;
        top: -10px;
        border-radius: 64px;
        color: #fff;
        font-size: 14px;
        font-weight: 500;
        padding: 3px 6px;
        background: #2a5275;
    }
}

.popup-more__item {
    border-bottom: 1px solid #c8cacb;
}
.popup-more__item-link {
    display: block;
    width: 100%;
    font-weight: 500;
    line-height: 130%;
    color: #070c11;
    padding: 15px 20px;
    transition: background-color 0.3s;
    @media (any-hover: hover) {
        &:hover {
            background-color: #dae4ed;
        }
    }
    &:focus-visible {
        outline: 2px solid #2a5275;
        border-radius: 1px;
        background-color: #dae4ed;
        transition: background-color 0.3s, outline 0s;
    }
    @media (max-width: 500px) {
        padding: 15px 10px;
    }
}
