.password-input {
    position: relative;
    min-height: 95px;
    padding-bottom: 20px;
    &__label {
        span {
            display: inline-block;
            margin-bottom: 10px;
        }
    }
    &__item {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            left: 20px;
            bottom: 0;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            background-image: url(../../assets/images/icons/popup-small/pass-icon.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }
    &__input {
        border: 2px solid #eaeaf0;
        width: 100%;
        border-radius: 5px;
        height: 48px;
        padding: 0 40px 0 50px;
        color: #070c11;
        font-size: 16px;
        transition: border 0.3s;
        &:focus-visible {
            border: 2px solid #4878a6;
        }
    }
    &__button {
        position: absolute;
        right: 10px;
        bottom: 13px;
        background-color: transparent;
        width: 24px;
        height: 24px;
        color: #a7a7a8;
        transition: all 0.3s;
        svg {
            color: inherit;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #4d7599;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 4px;
            border-radius: 1px;
            color: #4d7599;
            transition: color 0.3s, outline 0s;
        }
    }
    &__error {
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 14px;
        color: rgb(196, 40, 40);
    }
}
