.top-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
    @media (max-width: 768px) {
        min-height: 72px;
    }
    @media (max-width: 500px) {
        min-height: 60px;
    }
}
.top-header__logo {
    margin-right: 20px;
}
.logo {
    display: inline-block;
    &:focus-visible {
        outline: 2px solid #2a5275;
        border-radius: 1px;
    }
    img {
        width: 100%;
        max-width: 184px;
    }
}
.top-header__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.top-header__contacts {
    display: flex;
    flex-wrap: wrap;
    margin-right: 50px;
    @media (max-width: 1200px) {
        max-width: 200px;
    }
    @media (max-width: 950px) {
        display: none;
    }
}
.top-header__phone {
    display: block;
    white-space: nowrap;
    font-weight: 500;
    line-height: 100%;
    color: #070c11;
    margin-right: 20px;
    transition: all 0.3s;
    &:focus-visible {
        outline: 2px solid #2a5275;
        border-radius: 1px;
        transition: none;
    }
    @media (any-hover: hover) {
        &:hover {
            color: #4878a6;
        }
    }
}
.top-header__schedule {
    display: block;
    white-space: nowrap;
    font-weight: 500;
    color: #838688;
}
.top-header__navigation {
    display: flex;
    align-items: center;
    position: relative;
    height: 50px;
}
.top-header__search-body {
    @keyframes formShown {
        from {
            max-width: 107px;
        }
        to {
            max-width: 260px;
        }
    }
    height: 50px;
    &.shown {
        position: absolute;
        top: 0;
        right: 0;
        background-color: #fff;
        z-index: 2;
        width: 100%;
        height: 50px;
        .top-header__search-form {
            max-width: 260px;
            border-bottom: 1px solid #4878a6;
            animation: formShown 1s;
        }
    }
    @media (max-width: 768px) {
        display: none;
    }
}
.top-header__search-form {
    position: relative;
    padding: 12px 12px 12px 36px;
    border-bottom: 1px solid #c8cacb;
    max-width: 107px;
    min-width: 107px;
    margin-right: 22px;

    display: flex;
    align-items: center;
}
.top-header__search-input {
    flex: 1 1 100%;
    line-height: 19px;
    color: #070c11;
    font-size: 16px;
    padding: 0 8px;
    &::placeholder {
        line-height: 19px;
        color: #838688;
        font-size: 16px;
    }
}
.top-header__search-btn {
    position: absolute;
    top: 10px;
    left: 12px;
    border: 0;
    background: transparent;
    pointer-events: none;
}
.top-header__clear-btn {
    width: 12px;
    height: 12px;
    background-color: transparent;
    color: #070c11;
    color: rgba(#070c11, 0.8);
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
    pointer-events: none;
    transition: all 0.3s;
    &.shown {
        opacity: 1;
        visibility: visible;
        transform: scale(1);
        pointer-events: all;
        @media (any-hover: hover) {
            &:hover {
                color: #d73838;
                transform: scale(1.2);
            }
        }
    }
    svg {
        pointer-events: none;
    }
}
.top-header__search-results {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 5;
    max-width: 450px;
    @media (max-width: 768px) {
        display: none;
    }
}
.top-header__head-items {
    display: flex;
    align-items: center;
    margin-right: 20px;
    @media (max-width: 600px) {
        display: none;
    }
}
.top-header__head-item {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    svg {
        color: #838688;
        transition: all 0.3s;
    }
    &--cart {
        @media (max-width: 768px) {
            display: none;
        }
    }
    &--favorite {
        .top-header__item-link {
            @media (any-hover: hover) {
                &:hover svg {
                    color: rgb(233, 33, 33);
                }
            }
        }
    }
}
.top-header__item-link {
    @media (any-hover: hover) {
        &:hover svg {
            color: #2a5275;
        }
    }
    &:focus-visible {
        outline: 2px solid #2a5275;
        border-radius: 1px;
        transition: none;
    }
}
.top-header__item-quantity {
    position: absolute;
    left: 28px;
    top: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 20px;
    background: #2a5275;
    border-radius: 64px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    padding: 3px 6px;
    transform: scale(0);
    transition: transform 0.3s;
    &.visible {
        transform: scale(1);
    }
}
.top-header__auth {
    overflow: hidden;
    min-width: 102px;
    text-align: right;
    @media (max-width: 500px) {
        min-width: unset;
        text-align: unset;
    }
}
.top-header__auth-wrapper {
    padding: 4px;
}
.header__bottom {
    background-color: #0e1821;
    @media (max-width: 768px) {
        display: none;
    }
}
.bottom-header {
    position: relative;
}
.bottom-header__navigation {
    display: flex;
    align-items: center;
    min-height: 60px;
}
.bottom-header__menu {
    padding: 10px 20px;
}
.bottom-header__menu-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.bottom-header__menu-item {
    position: relative;
    white-space: nowrap;
    &:not(:last-child) {
        margin-right: 40px;
        @media (max-width: 1330px) {
            margin-right: calc(13px + 27 * ((100vw - 992px) / 338));
        }
        @media (max-width: 992px) {
            margin: 3px 13px 3px 0;
        }
    }
    &::after {
        position: absolute;
        content: "";
        bottom: -3px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #fff;
        transition: all 0.5s;
        transform: scale(0);
        border-radius: 5px;
    }
    &:has(.bottom-header__menu-link:focus-visible)::after {
        transform: scale(1);
    }
    @media (any-hover: hover) {
        &:hover::after {
            transform: scale(1);
        }
    }
    @media (max-width: 1000px) {
        font-size: 14px;
    }
}
.bottom-header__menu-link {
    color: #fff;
    &:focus-visible {
        outline: 2px solid #a33333;
        outline-offset: 5px;
        border-radius: 4px;
    }
}

.header-search-results {
    background-color: #fff;
    box-shadow: rgb(34 60 80 / 30%) 3px 6px 10px 1px;
    border-radius: 0px 0px 8px 8px;
    padding: 20px;
    max-height: 305px;
    overflow-y: auto;
    transform: translateY(50px);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0s, opacity 0.3s;
    &.shown {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        transition: all 0.5s;
    }
    &__item {
        &:not(:last-child) {
            border-bottom: 1px solid #070c11;
        }
    }
    &__link {
        display: grid;
        grid-template-columns: 50px 1fr;
        grid-gap: 5px 20px;
        padding: 10px 0;
        @media (any-hover: hover) {
            &:hover {
                .header-search-results__image {
                    transform: scale(1.2);
                }
                .header-search-results__title {
                    color: #4878a6;
                }
            }
        }
        &:focus-visible {
            outline: 2px solid #4878a6;
            outline-offset: 5px;
            .header-search-results__image {
                transform: scale(1.2);
            }
            .header-search-results__title {
                color: #4878a6;
            }
        }
    }
    &__image {
        width: 50px;
        height: 50px;
        object-fit: contain;
        grid-row: span 2;
        transition: transform 0.3s;
    }
    &__title {
        color: #070c11;
        transition: color 0.3s;
    }
    &__price {
        color: #070c11;
        font-weight: 700;
    }
    &__message {
        span {
            font-weight: 700;
        }
    }
    &__more {
        display: block;
        max-width: fit-content;
        margin: 10px auto 0;
        color: #4878a6;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #0a365f;
            }
        }
    }
}
