.popup-catalogue__item-link {
    padding: 8px 50px;
    display: flex;
    align-items: center;
    color: #070C11;
    font-weight: 500;
    line-height: 130%;
    transition: background-color .3s;
    &:focus-visible {
        outline: 2px solid #2a5275;
        outline-offset: -2px;
        border-radius: 1px;
        background-color: #dae4ed;
        transition: background-color .3s, outline 0s;
    }
    &.disabled {
       pointer-events: none;
       color: #9fa2a5;
       .popup-catalogue__item-img {
        opacity: 0.5;
       }
    }
    @media (any-hover: hover) {
        &:hover {
            background-color: #dae4ed;
        }
    }
    @media(max-width: 500px) {
        padding: 8px 15px;
    }
}
.popup-catalogue__item-img {
    margin-right: 10px;
}