@import "./assets/styles/reset";
@import "./assets/styles/normalize";
@import "./assets/styles/variables";
@import "./assets/styles/mixins";
@import "./assets/styles/fonts";

body {
    font-family: $fontFamily;
    font-size: 16px;
    position: relative;
    overflow-y: scroll;
}
main {
    flex: 1 0 auto;
    min-height: 50vh;
}
#root {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
}
.backdrop {
    background-color: rgba(#2a5275, 0.6);
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}
.backdrop-transparent {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 4;
    background: rgba(42, 82, 117, 0.4);
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}
.container {
    max-width: $maxWidthContainer + px;
    margin: 0 auto;
    padding: 0 15px;
    @media (max-width: 400px) {
        padding: 0 10px;
    }
}
.title {
    font-weight: 700;
    font-size: 28px;
    line-height: 130%;
    color: #070c11;
}
