.popup-small {
    position: absolute;
    top: 45vh;
    left: 50%;
    z-index: 11;
    transform: translate(-50%, -50%);
    max-width: 370px;
    width: 100%;
    max-height: 95vh;
    background-color: #ffffff;
    border-radius: 8px;
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;

    &.shown {
        opacity: 1;
        visibility: visible;
    }
    @media (max-width: 850px) {
        top: 20px;
        transform: translate(-50%, 0);
    }
    // .popup-small__header
    &__header {
        padding: 20px 30px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background-color: #edf2f6;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    // .popup-small__title
    &__title {
        margin-right: 20px;
    }
    // .popup-small__body
    &__body {
        padding: 20px 30px 30px 30px;
    }
    // .popup-small__item
    &__item {
        &--password {
            margin-bottom: 8px;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                left: 20px;
                bottom: 0;
                transform: translateY(-50%);
                width: 24px;
                height: 24px;
                background-image: url(./assets/images/icons/popup-small/pass-icon.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
    }
    // .popup-small__item-title
    &__item-title {
        margin-bottom: 8px;
    }
    // .popup-small__input
    &__input {
        border: 1px solid #eaeaf0;
        width: 100%;
        border-radius: 5px;
        height: 48px;
        padding: 0 20px;
        color: #070c11;
        font-size: 16px;
        &:focus {
            border: 1px solid #4878a6;
            box-shadow: 0px 5px 10px 2px rgba(#4878a6, 0.2);
            transition: all 0.3s;
        }
        &--password {
            padding: 0 50px;
        }
    }
    // .popup-small__password-visibility-btn
    &__password-visibility-btn {
        position: absolute;
        right: 10px;
        bottom: 13px;
        background-color: transparent;
        width: 24px;
        height: 24px;
        color: #a7a7a8;
        transition: all 0.3s;
        svg {
            color: inherit;
        }
        @media (any-hover: hover) {
            &:hover {
                color: #4d7599;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 4px;
            border-radius: 1px;
            color: #4d7599;
            transition: color 0.3s, outline 0s;
        }
    }
}
.breadcrumbs {
    padding: 40px 0;
    @media (max-width: 992px) {
        padding: 30px 0;
    }
    @media (max-width: 768px) {
        padding: 20px 0;
    }
    // .breadcrumbs__list
    &__list {
        display: flex;
        flex-wrap: wrap;
    }
    // .breadcrumbs__item
    &__item {
        @include adaptive-value(padding-right, 43, 28, 1);
        position: relative;
        &:not(:last-child) {
            &:after {
                content: "";
                position: absolute;
                top: 4px;
                @include adaptive-value(right, 15, 10, 1);
                width: 13px;
                height: 10px;
                background-image: url(./assets/images/icons/triangle.svg);
                background-position: center;
                background-repeat: no-repeat;
            }
        }
        &:not(:first-child) {
            // padding-left: 15px;
        }
        &:last-child {
            .breadcrumbs__link {
                color: #838688;
                pointer-events: none;
                @media (any-hover: hover) {
                    &:hover {
                        color: #838688;
                    }
                }
            }
        }
    }
    // .breadcrumbs__link
    &__link {
        color: #070c11;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            color: #4878a6;
        }
    }
}
.pagination {
    max-width: 400px;
    margin: 0 auto;
    @media (max-width: 500px) {
        max-width: 300px;
    }
    // .pagination__list
    &__list {
        display: flex;
        column-gap: 10px;
    }
    // .pagination__item
    &__item {
        flex: 0 0 48px;
        align-items: center;
        justify-content: center;
        height: 48px;
        border: 1px solid #eaeaf0;
        border-radius: 4px;
        background-color: #fff;
        transition: all 0.3s;
        &--left-arrow {
            background-image: url(./assets/images/icons/triangle-black.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 13px 8px;
            transform: rotate(90deg);
        }
        &--right-arrow {
            background-image: url(./assets/images/icons/triangle-black.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 13px 8px;
            transform: rotate(-90deg);
        }
        &--arrow-disabled {
            opacity: 0.3;
            border: 1px solid #a7a7a8;
            pointer-events: none;
            .pagination__link {
                cursor: default;
            }
        }
        &--current {
            pointer-events: none;
            background-color: #4878a6;
            .pagination__link {
                color: #ffffff;
            }
        }
        &--dots {
            font-weight: 700;
            font-size: 20px;
            border: 1px solid transparent;
        }
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
                .pagination__link {
                    color: #ffffff;
                }
            }
        }
        @media (max-width: 500px) {
            &:nth-child(4),
            &:nth-child(6) {
                display: none;
            }
        }
    }
    // .pagination__link
    &__link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #838688;
        width: 100%;
        height: 100%;
    }
}

.fade {
    animation-name: fade;
    animation-duration: 0.5s;
}
@keyframes fade {
    from {
        opacity: 0.1;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
