.backdrop {
    background-color: rgba(#2a5275, 0.6);
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 11;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s;
    &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
}
.backdrop-transparent {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 4;
    background: rgba(42, 82, 117, 0.4);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.6s;
    &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
}
