.popup-login {
    // .popup-login__item
    &__item {
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        // .popup-login__item--checkbox
        &--checkbox {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
    // .popup-login__item-title
    &__item-title {
        margin-bottom: 8px;
    }
    // .popup-login__input
    &__input {
        // .popup-login__input--password
        &--password {
            padding: 0 50px;
        }
    }
    // .popup-login__forgot-password
    &__forgot-password {
        display: inline-block;
        margin-bottom: 20px;
        color: #4878a6;
        @media (any-hover: hover) {
            &:hover {
                color: #0a365f;
                transition: color 0.3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 1px;
            color: #0a365f;
            transition: color 0.3s, outline 0s;
        }
    }
    // .popup-login__checkbox-input
    &__checkbox-input {
        display: none;
        &:checked + .popup-login__checkbox-label::after {
            transform: scale(1);
        }
    }
    // .popup-login__checkbox-label
    &__checkbox-label {
        position: relative;
        display: inline-flex;
        margin-bottom: 25px;
        line-height: 125%;
        align-items: center;
        white-space: nowrap;
        cursor: pointer;
        &::before {
            content: "";
            flex: 0 0 20px;
            align-self: start;
            height: 20px;
            background-color: #fff;
            border: 1px solid #eaeaf0;
            border-radius: 2px;
            margin-right: 7px;
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            background: url(../../assets/images/icons/popup-small/checkbox-icon.svg) 4px 5px/13px 9px no-repeat,
                #4878a6 0 0/20px 20px;
            border-radius: 2px;
            transform: scale(0);
            transition: transform 0.3s;
        }
        &.checked::after {
            transform: scale(1);
        }
        &:focus-visible {
            &::before {
                outline: 2px solid #2a5275;
                outline-offset: 1px;
                border-radius: 2px;
                transition: none;
            }
        }
    }
    // .popup-login__btn-login
    &__btn-login {
        display: block;
        color: #ffffff;
        font-weight: 500;
        font-size: 16px;
        padding: 16px 20px;
        background-color: #2a5275;
        width: 100%;
        border-radius: 4px;
        margin-bottom: 25px;
        @media (any-hover: hover) {
            &:hover {
                background-color: #4878a6;
                transition: background-color 0.3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 4px;
            border-radius: 4px;
            background-color: #4878a6;
            transition: background-color 0.3s, outline 0s;
        }
    }
    // .popup-login__btn-registration
    &__btn-registration {
        display: block;
        max-width: fit-content;
        margin: 0 auto;
        color: #4878a6;
        font-size: 16px;
        background: none;
        @media (any-hover: hover) {
            &:hover {
                color: #0a365f;
                transition: color 0.3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 4px;
            border-radius: 1px;
            color: #0a365f;
            transition: color 0.3s, outline 0s;
        }
    }
}
