.input-checkbox {
    &[data-checked="true"] {
        .input-checkbox__label::after {
            transform: scale(1);
        }
    }
    &__label {
        position: relative;
        padding-left: 30px;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            border: 2px solid #eaeaf0;
            border-radius: 4px;
            cursor: pointer;
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            background: url(../../assets/images/icons/popup-small/checkbox-icon.svg) 4px 5px/13px 9px no-repeat,
                #4878a6 0 0/20px 20px;
            border-radius: 4px;
            transform: scale(0);
            cursor: pointer;
            transition: transform 0.3s;
        }
    }
    &__input {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        border: 0;
        padding: 0;
        clip: rect(0 0 0 0);
        overflow: hidden;
        &:focus-visible + .input-checkbox__outline {
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            border-radius: 4px;
            outline: 2px solid #4878a6;
            outline-offset: 1px;
        }
    }
    &__outline {
    }
    &__description {
    }
}
