.popup-registration {
    // .popup-registration__item
    &__item {
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }
    &__body {
        position: relative;
    }
    // .popup-registration__item-title
    &__item-title {
        margin-bottom: 8px;
    }
    // .popup-registration__legacy
    &__legacy {
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #070c11;
        margin-bottom: 10px;
    }
    // .popup-registration__legacy-link
    &__legacy-link {
        color: #2a5275;
        @media (any-hover: hover) {
            &:hover {
                color: #062542;
                transition: 0.3s;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 2px;
            color: #062542;
            transition: color 0.3s, outline 0s;
        }
    }
    // .popup-registration__btn-registration
    &__btn-registration {
        display: block;
        color: #ffffff;
        font-weight: 500;
        font-size: 16px;
        padding: 16px 20px;
        background-color: #2a5275;
        width: 100%;
        border-radius: 4px;
        transition: background-color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #4878a6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            background-color: #4878a6;
            transition: background-color 0.3s, outline 0s;
        }
    }
}
