.popup-navigation {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background-color: #2a5275;
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    transition: transform 0.5s, opacity 0.5s, visibility 0.5s, padding 0s;
    @media (max-width: 768px) {
        display: block;
        &.hidden {
            transform: translateY(150%);
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transition: all 0.8s;
        }
    }
    &__list {
        display: flex;
        justify-content: space-around;
        align-items: center;
        min-height: 64px;
    }
    &__item {
        &:not(:last-child) {
            margin-right: 10px;
        }
    }
    &__link {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color: #bfcbd6;
        line-height: 123%;
        font-size: 13px;
        cursor: pointer;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.15);
                transition: transform 0.3s;
            }
        }
        svg {
            margin: 0 auto 3px;
        }
        &:focus-visible {
            outline: 2px solid #fff;
            outline-offset: 2px;
            border-radius: 2px;
            transform: scale(1.15);
            transition: transform .3s, outline 0s;
        }
    }
}

button.popup-navigation__link {
    background: none;
}