.bottom-header__catalogue {
    height: 100%;
    position: relative;
    z-index: 5;
    &.opened {
        .bottom-header__burger-line--1 {
            transform: rotate(45deg) translateY(10px);
        }
        .bottom-header__burger-line--2 {
            transform: translateX(-120%);
            opacity: 0;
            visibility: hidden;
        }
        .bottom-header__burger-line--3 {
            transform: rotate(-45deg) translateY(-10px);
        }
        .bottom-header__burger-point {
            transform: scale(0);
        }
        .bottom-header__catalogue-body {
            opacity: 1;
            visibility: visible;
            transform: translateY(0) scaleY(1);
            pointer-events: all;
        }
    }
}
.bottom-header__catalogue-box {
    width: 310px;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: #243C53;
    padding: 22px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color .5s;
    &:hover {
        background-color: #4878A6;
    }
    &:focus-visible {
        outline: 2px solid #a33333;
        outline-offset: -2px;
        border-radius: 1px;
        background-color: #4878A6;
    }
    @media (max-width: 1100px) {
        width: 250px;
    }
}
.bottom-header__catalogue-burger {
    position: relative;
    width: 24px;
    height: 16px;
}
.bottom-header__burger-line {
    position: absolute;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #fff;
    border-radius: 5px;
    opacity: 1;
    visibility: visible;
    transition: all .5s;
    &--1 {
        top: 0;
    }
    &--2 {
        top: 7px;
        width: 19px;
        left:auto;
        right: 0;
    }
    &--3 {
        bottom: 0;
    }
}
.bottom-header__burger-point {
    position: absolute;
    width: 2px;
    height: 2px;
    background-color: #fff;
    border-radius: 50%;
    top: 7px;
    left: 0;
    transition: all .5s;
}
.bottom-header__catalogue-title {
    color: #fff;
    margin-left: 10px;
}
.bottom-header__catalogue-body {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #EDF2F6;
    border-radius: 0px 0px 8px 8px;
    width: 100%;
    box-shadow: rgba(34, 60, 80, 0.2) 4px 0px 20px 4px;
    overflow: auto;
    max-height: 66vh;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-50%) scaleY(0);
    pointer-events: none;
    transition: all .5s;
}
.bottom-header__catalogue-list {
    display: flex;
    flex-direction: column;
    padding: 16px 0;
}
.bottom-header__catalogue-item {
    &:not(:last-child) {
        margin-bottom: 8px;
    }
}
.bottom-header__catalogue-link {
    display: flex;
    align-items: center;
    color: #070C11;
    font-weight: 500;
    line-height: 130%;
    background-color: inherit;
    padding: 0 18px;
    min-height: 48px;
    transition: background-color .3s;
    &:hover {
        background-color: #DAE4ED;
    }
    &.disabled {
        color: #9fa2a5;
        pointer-events: none;
        .bottom-header__catalogue-img {
            opacity: 0.5;
        }
    }
    &:focus-visible {
        outline: 2px solid #2a5275;
        outline-offset: -2px;
        background-color: #DAE4ED;
        transition: background-color .3s, outline 0s;
    }
}
.bottom-header__catalogue-img {
    margin-right: 10px;
}