.popup__body {
    position: relative;
    z-index: 1;
    max-width: 370px;
    width: 100%;
    max-height: 100%;
    background-color: #ffffff;
    border-radius: 8px;
    overflow-y: auto;
    &--large {
        max-width: unset;
        padding: 40px 0;
        @media(max-width: 500px) {
            padding: 15px 0;
        }
    }
}
